import { useBlockProps } from '@wordpress/block-editor';

// Reuse the indexHTML constant from edit.js
import indexHTML from 'html-loader!./my-website-files/index.html'; // Adjust the path as needed

export default function save() {
    return (
        <div { ...useBlockProps.save() }>
            {/* Embed your HTML content from index.html */}
            <div dangerouslySetInnerHTML={{ __html: indexHTML.default }}></div>
        </div>
    );
}
